/************************* SOCIAL ICONS animation *****************************************/
.social-icons ul {
  display: flex;
  justify-content: space-between;
  /*align-items: center;*/
   margin-left:1rem;
   transform: skew(15deg);
   transform-style: preserve-3d;
}
.social-icons ul li {
  position: relative;
  list-style: none;
  width: 30px;
  height: 30px;
  margin: 0px 20px;
}
.social-icons ul li:before{
  content: '';
  position: absolute;
  bottom: -10px;
  left: -5px;
  width: 100%;
  height: 10px;
  background: #fd6a02;
  trnasform-origin: top;
  transform: skewX(-41deg);
}
.social-icons ul li:after{
  content: '';
  position: absolute;
  top:5px;
  left: -9px;
  width: 9px;
  height: 100%;
  background: #fd6a02;
  trnasform-origin: right;
  transform: skewY(-49deg);
}
.social-icons ul li span{
  position: absolute;
  top: 0;
  lef: 0;
  width: 100%;
  height: 100%;
  display: flex !important;
  background: #fd6a02;
  justify-content: center;
  align-items: center;
  color: #fff;
  font-size: 30px !important;
  transition: 1.5s ease-out;
}
.social-icons ul li:hover span {
  z-index: 1000;
  transition: .3s;
  color: #fff;
  box-shadw: -1px 1px 1px rgba(0, 0, 0, .5);
}
.social-icons ul li:hover span:nth-child(5){
  transform: translate(40px, -40px);
  opacity: 1;
}
.social-icons ul li:hover span:nth-child(4){
  transform: translate(30px, -30px);
  opacity: .8;
}
.social-icons ul li:hover span:nth-child(3){
  transform: translate(20px, -20px);
  opacity: .6;
}
.social-icons ul li:hover span:nth-child(2){
  transform: translate(10px, -10px);
  opacity: .4;
}.social-icons ul li:hover span:nth-child(1){
  transform: translate(0px, 0px);
  opacity: .2;
}
.social-icons ul li:nth-child(1):hover span{
  background: #3b5998 !important;
}
.social-icons ul li:nth-child(2):hover span{
  background: #00acee !important;
}
.social-icons ul li:nth-child(3):hover span{
  background: #FD1D1D !important;
}
.social-icons ul li:nth-child(4):hover span{
  background: #f5f5f5 !important;
}
.social-icons ul li:nth-child(5):hover span{
  background: #0e76a8 !important;
}
